<template>
<html lang="ja"><!--日本語のhtml文書だよの宣言-->
  <head><!--ページ情報記述-->
    <meta charset="UTF-8"><!--文字化けしないために必須-->
    <link rel="icon" type="png" href="./assets/logo_white.png">
    <title>シャキルスタジオメンバー募集</title><!--ブラウザタブ、ブックマークしたときのタイトル-->
    <meta name="description" content="シャキルスタジオ（仮）の再始動に際し、新たなメンバーを募集する。"><!--ページの簡単な説明-->
  </head>
  <body>
    <div id="loading">
      <div class="shakilstudio1"><!--ロゴ-->
          <img src="./assets/logo_white.png" alt="ShakilStudio_logo" class="logo">
          <h1>SHAKIL STUDIO</h1>
          <h2>is getting ready.</h2>
      </div>
    </div>
    <div class="main">
      <NiceHeader/>
      <NiceTop/>
      <NiceAbout/>
      <NiceWorks/>
      <NiceRecords/>
      <NiceForm/>
      <NiceFooter/>
      <!-- <HelloWorld/> -->
    </div>
  </body>
</html>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'
import NiceHeader from './components/Header.vue'
import NiceTop from './components/Top.vue'
import NiceAbout from './components/About.vue'
import NiceWorks from './components/Works.vue'
import NiceRecords from './components/Records.vue'
import NiceForm from './components/Form.vue'
import NiceFooter from './components/Footer.vue'

export default {
  name: 'App',
  components: {
    NiceHeader,
    NiceTop,
    NiceAbout,
    NiceWorks,
    NiceRecords,
    NiceForm,
    NiceFooter
  }
}
const sleep = (time) => new Promise((resolve) => setTimeout(resolve, time));
window.onload=async()=>{
  const loading = document.getElementById('loading')
  await sleep(3000);
  loading.classList.add('loaded')
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
html {
  font-size: 100%;
  scroll-behavior: smooth;
}/*後でemとかrem使えるようにfont-sizeの基準決めとく。スクロールを滑らかに*/
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}/*変な余白があると嫌なので*/
.main {
  width: 100%;
  height: 100vh;
  background: white;
}
#loading {
  transition: all 1s;
  background: #082B71;
  position: fixed;
  z-index: 9999;
  inset: 0;
  display: grid;
  place-items: center;
}
.shakilstudio1 {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: white;
}
.logo {
  width: 37px;
  height: 37px;
  margin: 15px;
}
.shakilstudio1 h1{
  margin-right: 30px;
}
.loaded {
  opacity: 0;
  visibility: hidden;
}
</style>
